import {
    SET_CITY,
    SET_PATIENT,
    SET_AREA,
    SET_HOSPITAL_TYPE,
    SELECTED_HOSPITAL,
    SET_MONEY
} from '../mutation-types'

const state = {
    selectedCity: '',
    patient: {},
    selectedHospital: {},
    money: 10,
    hospitals: [{
        name: '',
        area: 3,
        type: 1,
        typeName: '',
        address: '3015'
    },{
        name: '',
        area: 3,
        type: 1,
        typeName: '',
        address: '1017'
    },{
        name: '',
        area: 3,
        type: 2,
        typeName: '',
        address: '1080'
    },{
        name: '',
        area: 3,
        type: 3,
        typeName: '',
        address: '83'
    },{
        name: '',
        area: 3,
        type: 3,
        typeName: '',
        address: '2021'
    },{
        name: '',
        area: 1,
        type: 1,
        typeName: '',
        address: '1120'
    },{
        name: '',
        area: 1,
        type: 2,
        typeName: '',
        address: '1120'
    },{
        name: '',
        area: 1,
        type: 3,
        typeName: '',
        address: '72'
    },{
        name: '',
        area: 2,
        type: 3,
        typeName: '',
        address: '2051'
    },{
        name: '',
        area: 2,
        type: 4,
        typeName: '',
        address: '10'
    },{
        name: '',
        area: 4,
        type: 3,
        typeName: '',
        address: '2010'
    },{
        name: '',
        area: 4,
        type: 4,
        typeName: '',
        address: '2'
    },{
        name: '',
        area: 5,
        type: 1,
        typeName: '',
        address: '3025'
    },{
        name: '',
        area: 5,
        type: 3,
        typeName: '',
        address: '4253'
    },{
        name: '',
        area: 6,
        type: 1,
        typeName: '',
        address: '1228'
    }],
    area: [{
        id: 0,
        name: '',
        selected: true
    },{
        id: 1,
        name: '',
        selected: false
    },{
        id: 2,
        name: '',
        selected: false
    },{
        id: 3,
        name: '',
        selected: false
    },{
        id: 4,
        name: '',
        selected: false
    },{
        id: 5,
        name: '',
        selected: false
    },{
        id: 6,
        name: '',
        selected: false
    }],
    hospitalType: [{
        id: 0,
        name: '',
        selected: true
    },{
        id: 1,
        name: '',
        selected: false
    },{
        id: 2,
        name: '',
        selected: false
    },{
        id: 3,
        name: '',
        selected: false
    },{
        id: 4,
        name: '',
        selected: false
    }]
}

const mutations = {
    [SET_CITY](state, city) {
        state.selectedCity = city;
    },
    [SET_PATIENT](state, patient) {
        state.patient = patient;
    },
    [SET_AREA](state, id) {
        state.area.forEach((item) => {
            if (item.selected) {
                item.selected = false;
            } else if (id == item.id) {
                item.selected = true;
            }
        })
    },
    [SET_HOSPITAL_TYPE](state, id) {
        state.hospitalType.forEach((item) => {
            if (item.selected) {
                item.selected = false;
            } else if (id == item.id) {
                item.selected = true;
            }
        })
    },
    [SELECTED_HOSPITAL](state, hospital) {
        state.selectedHospital = hospital;
    },
    [SET_MONEY](state, money) {
        state.money = money;
    }
}

export default {
    state,
    mutations
}


