<template>
<div class="page-full component-news-detail fx-column"> 
    <app-header :title="appHeader.title">
        <div slot="right">
            <i class="icon-shoucang iconfont ac-o"></i>
            <i class="icon-fenxiang iconfont ac-o"></i>
        </div>
    </app-header>
    <div class="page-content fx-1">
        <div class="d-header">
            <h2 class="d-header-t">
            <ul class="tag-group">
                <li class="tag-i" v-text="ingredient"></li>
                <li class="tag-i" v-text="prescription"></li>
                <li class="tag-i" v-text="insurance"></li>
            </ul>
            <p class="d-header-tit" v-text="item.general_name || '--'"></p>
        </h2>
            <p class="d-header-d" v-text="'' + item.production_enterprises || '--'"></p>
            <p class="d-header-d" v-text="'' + item.dosage_form || '--'"></p>
            <p class="d-header-d" v-text="'' + (item.price || '--') + '/' + (item.specification || '--')"></p>
        </div>
        <ul class="d-nav">
            <li class="d-nav-i" @click="type = 1" :class="{ active: type == 1 }"></li>
            <li class="d-nav-i" @click="type = 2" :class="{ active: type == 2 }"></li>
        </ul>
        <div class="drug-main">
            <div v-show="type == 1" class="drug-one">
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.major_ingredient || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item._character || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.disease || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.contraindicant || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item._usage || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.store || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.period || '--'"></p>
                </div>
                <div v-if="pictures" class="drug-images swiper-container">
                    <div class="swiper-wrapper">
                        <div v-for="(item, index) in pictures" class="swiper-slide drug-img"><img src="../../assets/images/aid/default.png" @error="errorImg" /></div>
                    </div>
                    <!--  -->
                    <div class="swiper-pagination"></div>
                    <!--  
                    <div class="swiper-button-prev"></div>
                    <div class="swiper-button-next"></div>-->
                </div>
                <div class="drug-footer" v-text="infoText"></div>
            </div>
            <div v-show="type == 2" class="drug-two">
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.approval_numbers || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.execution_standard || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.for_symptom || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.poor_reactions || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.notes || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.pregnant_notes || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.children_notes || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.old_notes || '--'"></p>
                </div>
                <div class="drug-item">
                    <strong></strong>
                    <p v-html="item.pharmacological || '--'"></p>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import appHeader from '../../components/app-header.vue'

export default {
    components: { appHeader },
    data (){
        return {
            appHeader: {
                title: ''
            },
            item: {"id":"33","general_name":"","commodity_name":"","spell_name":"","ingredient_label":"2","ingredient":"","price":"86.00","prescription_label":"4","prescription":"","insurance_label":"9","insurance":"","disease":",,,,,,,,,","dosage_form":"()","dosage":"1","_character":"","major_ingredient":"","auxiliary_ingredient":"","for_symptom":",,,,,,","specification":"0.3g","poor_reactions":",","_usage":".8-10,3.","contraindicant":"","notes":"","pregnant_notes":"","children_notes":"","old_notes":"","drug_interactions":"","pharmacological":"1.DNARNA<br>2.T<br>3.<br>4.<br>5.","pharmacokinetics":"","store":"","period":"36","execution_standard":"","approval_numbers":"Z20054999","production_enterprises":"()","avg_score":null,"picture":"default.png"},
            type: 1,
            infoText: '',
        }
    },
    computed: {
        ingredient: function(){
            var _ingredient = '';
            if (this.item.ingredient == '' || this.item.ingredient == '') {
                _ingredient = '';
            } else if (this.item.ingredient == '') {
                _ingredient = '';
            }
            return _ingredient;
        }, 
        prescription: function(){
            var _prescription = '';
            if (this.item.prescription == '' || this.item.prescription == '' || this.item.prescription == '' || this.item.prescription == '' || this.item.prescription == '') {
                _prescription = '';
                this.infoText = '';
            } else if (this.item.prescription == 'OTC' || this.item.prescription == 'OTC' || this.item.prescription == 'OTC') {
                _prescription = 'OTC';
                this.infoText = '';
            } else if (this.item.prescription == 'OTC' || this.item.prescription == 'OTC') {
                _prescription = 'OTC';
                this.infoText = '';
            }
            return _prescription;
        },
        insurance: function(){
            var _insurance = '';
            if (this.item.insurance == '' || this.item.insurance == '') {
                _insurance = '';
            } else if (this.item.insurance == '' || this.item.insurance == '' || this.item.insurance == '' || this.item.insurance == '' || this.item.insurance == '') {
                _insurance = '';
            } else if (this.item.insurance == '' || this.item.insurance == '') {
                _insurance = '';
            }
            return _insurance;
        },
        pictures: function(){
            var _pictures = null;
            if (this.item.picture) {
                _pictures = this.item.picture.split(',');
            }
            return _pictures;
        }
    },
    methods: {
        errorImg: function (event) {
            var _this = event.target;
            if (_this.retry && _this.src) {
                return;
            } else {
                _this.retry = true;
                _this.src = null;
                _this.src = 'images/default.png';
            }
        }
    }
}
</script>

<style lang="less" scoped>
@import "../../assets/css/common.less";
.drug-bg{
    background-color: @bg_main;
}

.d-header{
    padding: 0.32rem 0.2rem;
    overflow: hidden;
    background-color: #fff;
    & .tag-group{
        overflow: hidden;
        float: right;
        margin-top: 0.02rem;
    }
}
.d-header-t{
    margin-bottom: 0.2rem;
    line-height: 1.25em;
    font-size: 0.34rem;
    font-weight: bold;
    color: @color_l;
}
.d-header-d{
    margin-bottom: 0.12rem;
    line-height: 1.2em;
    font-size: 0.24rem;
    color: @color_s;
    &:last-child{
        margin-bottom: 0;
    }
}

.tag-group{
    .clearfix()
}
.tag-i{
    float: left;
    margin-left: 0.1rem;
    padding: 0.06rem;
    font-size: 0.2rem;
    font-weight: normal;
    line-height: 1;
    color: #999;
    text-align: center;
    border: 1px solid #999;
}

.d-nav{
    height: 1.02rem;
    border: 1px solid @bg_bd;
    display: flex;
    background-color: #fff;
}
.d-nav-i{
    display: flex;
    flex: 1;
    height: 100%;
    font-size: 0.32rem;
    color: @color_l;
    justify-content: center;
    align-items: center;
    border-bottom: .04rem solid transparent;
    &.active{
        color: @color_b;
        border-color: @bg;
    }
}

.drug-main{
    padding: 0.28rem 0.2rem 0 0.2rem;
    font-size: 0.28rem;
    background-color: @bg_cont;
}
.drug-item{
    padding-bottom: 0.3rem;
    line-height: 0.5rem;
    & strong{
        font-weight: bold;
        color: @color_l;
    }
}

.drug-images{
    margin: 0 -0.2rem;
    padding: 0.4rem 0.2rem;
    text-align: center;
    background-color: #fff;
}
.drug-footer{
    margin: 0 -0.2rem;
    padding: 0.16rem 0.2rem;
    line-height: 1.4;
    font-size: 0.18rem;
    color: #9fa1a9;
    background-color: @bg_main;
}
.swiper-pagination-bullet-active{
    background: @bg;
}
</style>



