<template>
<div class="page-full component-news-detail fx-column"> 
    <app-header :title="appHeader.title">
        <div slot="right">
            <i class="icon-shoucang iconfont ac-o"></i>
            <i class="icon-fenxiang iconfont ac-o"></i>
        </div>
    </app-header>
    <div class="page-content fx-1">
        <div class="app-content firstaid-content">
            <div class="content detail-cont">
                <div class="detail-head detail-bg border-b">
                    <h2 class="detail-head-title">{{item.c_name}}</h2>
                    <p class="detail-head-small"></p>
                    <span @click="hasPop = !hasPop" class="warn-block fx-c">!</span>
                </div>
                <div class="detail-info">
                    <!--  -->
                    <!-- 1 -->
                    <div v-if="stepArr" v-for="(value, index) in stepArr" class="border-b info-group">
                        <div v-if="value.tit" class="info-h"><span class="fillet-block"></span>{{value.tit}}</div>
                        <div v-if="value.desc" class="info-desc">{{value.desc}}</div>
                        <!--  -->
                        <div v-if="value.arrs && value.arrs.length > 0" v-for="(value1, index1) in value.arrs" class="info-sub">
                            <p class="info-sub-h info-tag"><span class="round-block">{{index1 + 1}}</span>{{value1.tit}}</p>
                            <ol v-if="value1.arrs && value1.arrs.length > 0" class="info-sub-list disc-list">
                                <li v-for="(value2, index2) in value1.arrs">{{value2}}</li>
                            </ol>
                        </div>
                    </div>
                    <!--  -->
                    <div v-if="doctorArr" class="tips info-group info-group-doctor">
                        <p class="info-sub-h"></p>
                        <div v-for="(value, index) in doctorArr">
                            <p v-if="value.tit" class="info-sub-tips">{{value.tit}}</p>
                            <ol v-if="value.arrs && value.arrs.length > 0" class="info-sub-list disc-list">
                                <li v-for="(value1, index1) in value.arrs">{{value1}}</li>
                            </ol>
                        </div>
                    </div>
                </div>
            </div>
            <div v-show="hasPop" class="pop-cover pop-bg">
                <div class="pop-cont">
                    <div class="pop-h">&nbsp;</div>
                    <div class="pop-b">
                        <div class="pop-b-box clearfix" v-for="(value, index) in makeArr">
                            <p v-if="value.tit" class="pop-b-tit">{{value.tit}}</p>
                            <ol v-if="value.arrs && value.arrs.length > 0" class="pop-list disc-list clearfix">
                                <li v-for="(value1, index1) in value.arrs" class="pop-li">{{value1}}</li>
                            </ol>
                        </div>
                    </div>
                    <div class="pop-f fx">
                        <a href="tel:120" class="pop-btn pop-btn-l bg fx-1 fx-c"><i class="iconfont icon-dianhua"></i>120</a>
                        <a @click="hasPop = false" href="javascript:;" class="pop-btn pop-btn-r fx-1 fx-c"><i class="iconfont icon-gou"></i></a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import appHeader from '../../components/app-header.vue'

export default {
    components: { appHeader },
    data (){
        return {
            appHeader: {
                title: ''
            },
            item: {"id":"198","e_name":"Vomiting (Children)","c_name":"","makecall":"","do_step1":"^@|#@|35|||4560&&*^@2030|||15#@#34@#8@|#24@ ","do_step2":"<br>\r<br> 1. <br>    2030<br>    <br>    <br>    15<br>2. <br>    <br>3. 34<br>    <br>4. 8<br>    <br>    <br>5. 24<br>    ","do_step3":null,"do_step4":null,"do_step5":null,"do_step6":null,"do_step7":null,"go_doctor":"|||||||38.5|||","appreciation":"0","remarks":null,"last_update_by":null,"last_update_time":null},
            hasPop: false,
        }
    },
    computed: {
        makeArr: function(){
            return this.getArr(this.item.makecall);
        },
        doctorArr: function(){
            return this.getArr(this.item.go_doctor);
        },
        stepArr: function(){
            if(!this.item.do_step1){
                return null;
            }
            var arr = this.item.do_step1.split('&&');
            var stepArr = [];
            for(var i = 0, l = arr.length; i < l; i++){
                var obj = {};
                var arr1 = arr[i].split('^');
                var arrs = [];
                if(arr1.length > 1){
                    var subArr = arr1[0].split('*');
                    if(subArr.length > 1){
                        obj.desc = subArr[1]
                    }
                    obj.tit = subArr[0];
                    arrs = arr1[1];
                }else{
                    arrs = arr1[0];
                }
                obj.arrs = [];
                var arr2 = arrs.split('#');
                for(var j = 0, k = arr2.length; j < k; j++){
                    var obj2 = {};
                    var arr3 = arr2[j].split('@');
                    var arr4 = [];
                    obj2.arrs = [];
                    if(arr3.length > 1){
                        obj2.tit = arr3[0];
                        arr4 = arr3[1].split('|');
                    }else{
                        arr4 = arr3[0].split('|');
                    }
                    for(var n = 0, m = arr4.length; n < m; n++){
                        obj2.arrs.push(arr4[n]);
                    }
                    obj.arrs.push(obj2);
                }
                stepArr.push(obj);
            }
            return stepArr;
        }
    },
    methods: {
        getArr: function(data){
            if(!data){
                return null;
            }
            var arr = data.split('&&');
            var curArr = [];
            for(var i = 0, l = arr.length; i < l; i++){
                var arr1 = arr[i].split('#');
                var arr2 = [];
                var obj = {};
                obj.arrs = [];
                if(arr1.length > 1){
                    obj.tit = arr1[0];
                    if(arr1[1]){
                        arr2 = arr1[1].split('|');
                    }
                }else{
                    arr2 = arr1[0].split('|');
                }
                for(var j = 0, k = arr2.length; j < k; j++){
                    obj.arrs.push(arr2[j]);
                }
                curArr.push(obj);
            }
            return curArr;
        }
    },
    created () {
    }
}
</script>

<style lang="less" scoped>
@import "../../assets/css/common.less";
.firstaid-content .detail-head-title{
    margin-right: 1rem;
}
.warn-block {
    background-color: @bg_info;
    color: @color_f;
    width: 0.9rem;
    height: 0.34rem;
    border-radius: 0.02rem;
    font-size: .24rem;
    position: absolute;
    right: .2rem;
    top: .39rem
}
.pop-cover{
    top: .88rem;
    background: rgba(0, 0, 0, 0.2);
}

.pop-cont {
    margin-top: -.44rem;
    width: 6rem;
    .pop-h, .pop-f {
        height: 1rem;
        line-height: 1rem;
        color: @color_f;
        text-align: center;
        font-size: .38rem;
    }
    .pop-h {
        background-color: @bg_info;
        border-top-right-radius: .1rem;
        border-top-left-radius: .1rem;
    }
    .pop-f {
        border-bottom-right-radius: .1rem;
        border-bottom-left-radius: .1rem;

        .pop-btn.bg{
            background-color: #61c5fc;
        }
        .pop-btn {
            background-color: @bg;
            color: @color_f;
        }
    }
    .pop-b {
        background: #fff;
        min-height: 2.8rem;
        max-height: 7rem;
        padding: .2rem 0 .44rem 0;
        -webkit-overflow-scrolling: touch; 
        overflow-y: auto;
    }
}

.pop-list .pop-li{
    line-height: 1.5em;
    max-height: 3em;
    overflow: hidden;
    padding-left: .23rem;
    float: left;
    min-width: 50%;
    margin-bottom: .1rem;
}
.info-desc{
    margin: -0.08rem 0.28rem 0.24rem 0.28rem;
    line-height: 0.3rem;
    font-size: 0.24rem;
    color: #9fa1a9;
}
.pop-b-box{
    padding-top: 0.24rem;
    & .disc-list{
        margin-left: .48rem;
        margin-right: .24rem;
    }
    & .disc-list li{
        margin-left: 0;
        padding-right: .24rem;
    }
}
.pop-b-tit{
    margin: 0 .48rem .1rem .48rem;
    line-height: 1.5em;
    font-weight: bold;
    color: @color_l;
}
.info-group-doctor .disc-list li{
    margin-left: .1rem;
}
.pop-btn-l{
    border-bottom-left-radius: .1rem;
    & .iconfont{
        margin-right: .15rem;
    }
}
.pop-btn-r{
    border-bottom-right-radius: .1rem;
}
.info-tag.info-sub-h{
    padding: 0 0 0.2rem 0.48rem;
    line-height: 1.5;
}
.info-tag .round-block{
    margin-left:  -0.43rem;
}
</style>



