                    <li class="fx-1 fx-c"><i>1</i></li>
                    <li class="fx-1 fx-c"><i>2</i></li>
                    <li class="fx-1 fx-c"><i>3</i></li>
                    <li class="fx-1 fx-c"><i>4</i></li>
                </ul>
            </section>
            <section class="accompany-content">
                <a class="fx-c" @click="noticeFn" href="javascript:void(0)"></a>
                <h2></h2>
                <h3></h3>
                <p></p>
                <p></p>
                <h3></h3>
                <p></p>
                <p></p>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
            </section>
        </div>
        <div v-show="type == 2" class="accompany-wraper">
            <section class="accompany-top">
                <h2></h2>
                <p>07:00-19:00</p>
                <div><span>10</span></div>
            </section>
            <section class="accompany-process">
                <h2></h2>
                <ul class="fx">
                    <li class="fx-1 fx-c"><i>1</i></li>
                    <li class="fx-1 fx-c"><i>2</i></li>
                    <li class="fx-1 fx-c"><i>3</i></li>
                    <li class="fx-1 fx-c"><i>4</i></li>
                </ul>
            </section>
            <section class="accompany-content">
                <a class="fx-c" @click="noticeFn" href="javascript:void(0)"></a>
                <h2></h2>
                <h3></h3>
                <p></p>
                <p></p>
                <h3></h3>
                <p></p>
                <p></p>
                <p></p>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
            </section>
        </div>
        <div v-show="type == 3" class="accompany-wraper">
            <section class="accompany-top">
                <h2></h2>
                <p>07:00-19:00</p>
                <div><span>10</span></div>
            </section>
            <section class="accompany-process">
                <h2></h2>
                <ul class="fx">
                    <li class="fx-1 fx-c"><i>1</i></li>
                    <li class="fx-1 fx-c"><i>2</i></li>
                    <li class="fx-1 fx-c"><i>3</i></li>
                    <li class="fx-1 fx-c"><i>4</i></li>
                </ul>
            </section>
            <section class="accompany-content">
                <a class="fx-c" @click="noticeFn" href="javascript:void(0)"></a>
                <h2></h2>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
            </section>
        </div>
