            <section class="accompany-top">
                <h2></h2>
                <p>07:00-19:00</p>
                <div><span>10</span></div>
            </section>
            <section class="accompany-process">
                <h2></h2>
                <ul class="fx">
                    <li class="fx-1 fx-c"><i>1</i></li>
                    <li class="fx-1 fx-c"><i>2</i></li>
                    <li class="fx-1 fx-c"><i>3</i></li>
                    <li class="fx-1 fx-c"><i>4</i></li>
                </ul>
            </section>
            <section class="accompany-content">
                <a class="fx-c" @click="noticeFn" href="javascript:void(0)"></a>
                <h2></h2>
                <h3></h3>
                <p></p>
                <p></p>
                <h3></h3>
                <p></p>
                <p></p>
                <p></p>
                <h3></h3>
                <p></p>
                <h3></h3>
                <p></p>
            </section>
        </div>
    </div>
    <router-link tag="div" to="/appointment" class="app-bottom fx-c ac-o">{{accompanyName}}</router-link>
    <div v-if="hasNotice" class="pop"></div>
    <transition name="error">
        <div v-if="hasNotice" class="accompany-notice">
            <h2 class="fx-c"></h2>
            <div class="accompany-notice-text">
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <p>4400-921-5800</p>
            </div>
            <a @click="hasNotice = false" class="accompany-notice-btn fx-c ac-o" href="javascript:void(0)"><i class="iconfont icon-gou"></i></a>
        </div>
    </transition>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import appHeader from '../components/app-header.vue'

export default {
    components: { appHeader },
    data (){
        return {
            appHeader: {
                title: '',
                backUrl: '/home'
            },
            accompanyName: '',
            hasNotice: false,
            type: 1
        }
    },
    computed: {
        ...mapGetters([
          'selectedCity'
        ])
    },
    methods: {
        noticeFn: function(){
            this.hasNotice = true;
        },
        toPage: function(type){
            if(this.type != type){
                this.type = type;
            }
        }
    },
    watch: {
        type: function(val){
            switch (val) {
                case 1: 
                    this.accompanyName = ''
                    break;
                case 2:
                    this.accompanyName = ""
                    break;
                case 3:
                    this.accompanyName = ""
                    break;
                case 4:
                    this.accompanyName = ""
                    break;
                default:
                    this.accompanyName = ''
            }
        }
    }
}
</script>

<style lang="less" scoped>
@import "../assets/css/common.less";
.page-content{
    background-color: #f2f3f5;
}
.accompany-header{
    background-color: #39b7ff;
}
.accompany-nav{
    height: 1rem;
}
.accompany-nav-item{
    height: 1rem;
    font-size: .6rem;
    color: #a6dfff;
    border-bottom: 0.05rem solid transparent;
    border-radius: 1px;
    &.active{
        color: #fff;
        border-color: #a6dfff;
    }
}

.accompany-top, .accompany-process, .accompany-content{
    padding: 0.4rem 0.2rem;
    background-color: #fff;
}
.accompany-top{
    position: relative;
    margin-bottom: .2rem;
    & h2{
        margin-bottom: 0.3rem;
        font-size: 0.34rem;
        color: #3d4550;
    }
    & p{
        font-size: 0.24rem;
        color: #7b8ea9;
    }
    & div{
        position: absolute;
        top: .54rem;
        right: .2rem;
        color: #ff6476;
    }
    & span{
        font-size: 0.6rem;
    }
}

.accompany-process{
    margin-bottom: 0.2rem;
    & h2{
        margin-bottom: 0.3rem;
        font-size: 0.24rem;
        color: #7b8ea9;
    }
    & li{
        margin-left: 0.14rem;
        height: .6rem;
        font-size: 0.22rem;
        color: #646f7f;
        border: 1px solid #c5c7ce;
        border-radius: 0.03rem;
        &:first-child{
            margin-left: 0;
        }
        & i{
            display: inline-block;
            width: .22rem;
            height: .22rem;
            margin-right: 0.06rem;
            line-height: .22rem;
            text-align: center;
            font-size: 0.18rem;
            color: #fff;
            border-radius: 0.22rem;
            background-color: #7b8ea9;
        }
    }
}

.accompany-content{
    position: relative;
    & a{
        position: absolute;
        top: 0.32rem;
        right: 0.2rem;
        width: 1.2rem;
        height: 0.4rem;
        font-size: .22rem;
        color: #39b8ff;
        border-radius: 0.03rem;
        border: 1px solid #39b8ff;
    }
    & h2{
        font-size: 0.24rem;
        color: #7b8ea9;
    }
    & h3{
        margin-top: .3rem;
        margin-bottom: .1rem;
        font-size: .3rem;
        color: #3d4550;
    }
    & p{
        line-height: 1.714;
        font-size: .28rem;
        color: #646f7f;
    }
}

.component-accompany .app-bottom{
    background-color: #39b7ff;
}

.accompany-notice{
    position: absolute;
    z-index: 10;
    //margin: 1rem 0.35rem 0 0.35rem;
    top: 1rem;
    right: 0.35rem;
    left: 0.35rem;
    border-radius: 0.1rem;
    background-color: #fff;
    & h2{
        height: 1.2rem;
        font-size: 0.38rem;
        color: #3d4550;
        border-bottom: 1px solid #e6ecf1;
    }
}
.accompany-notice-text{
    padding: 0.5rem 0.46rem 0.6rem 0.46rem;
    font-size: 0.28rem;
    color: #65707f;
    line-height: 1.785;
}
.accompany-notice-btn{
    height: 1rem;
    font-size: 0.38rem;
    color: #fff;
    background-color: #39b7ff;
}
</style>



