<template> 
<div class="page-full component-selectcity fx-column"> 
    <app-header :title="appHeader.title"></app-header>
    <div class="selectcity-top">
        <i class="iconfont icon-dingwei"></i><span>{{curCity}}</span>
    </div>
    <div class="page-content fx-1">
        <div id="scroll-left">
            <ul>
                <li v-for="(subject, index) in subjects" :key="subject.id" class="ac" :class="{'active': index == curSubjectIndex}"  @click="selectSubject(index)"><span>{{subject.name}}</span></li>
            </ul>
        </div>
        <div id="scroll-right">
            <ul>
                <li v-for="(item, index) in citys" :key="index"><a @click="toPage(item)" href="javascript:void(0)">{{item}}</a></li>
            </ul>
        </div>
    </div>
</div>
</template>

<script>

import IScroll from 'iscroll'
import Utils from '../../utils'
import { mapGetters, mapActions } from 'vuex'
import appHeader from '../../components/app-header.vue'

var myScroll = null;

export default {
    components: { appHeader },
    data () {
        return {
            appHeader: {
                title: ''
            },
            curSubjectIndex: 0,
            subjects: [{
                id: 1,
                name: '',
                city: ['', '']
            },{
                id: 2,
                name: '',
                city: ['']
            },{
                id: 3,
                name: '',
                city: ['', '', '']
            },{
                id: 4,
                name: '',
                city: ['', '']
            },{
                id: 5,
                name: '',
                city: ['', '']
            },{
                id: 6,
                name: '',
                city: ['']
            },{
                id: 7,
                name: '',
                city: ['']
            },{
                id: 8,
                name: '',
                city: ['', '']
            },{
                id: 9,
                name: '',
                city: ['', '']
            },{
                id: 10,
                name: '',
                city: ['']
            },{
                id: 11,
                name: '',
                city: ['', '']
            },{
                id: 12,
                name: '',
                city: ['']
            },{
                id: 13,
                name: '',
                city: ['', '']
            },{
                id: 14,
                name: '',
                city: ['']
            },{
                id: 15,
                name: '',
                city: ['']
            },{
                id: 16,
                name: '',
                city: ['']
            },{
                id: 17,
                name: '',
                city: ['']
            },{
                id: 18,
                name: '',
                city: ['']
            },{
                id: 19,
                name: '',
                city: ['', '']
            },{
                id: 20,
                name: '',
                city: ['']
            }]
        }
    },
    methods: {
        selectSubject: function (index) {
            this.curSubjectIndex = index;
        },
        toPage: function(item){
            const city = item.replace('', '');
            this.$store.dispatch('set_city', city);
            this.$router.push({ path: 'accompany'});
        }
    },
    computed: {
        ...mapGetters([
          'curCity'
        ]),
        citys () {
            return this.subjects[this.curSubjectIndex]['city']
        }
    },
    mounted () {
        this.$nextTick(() => {
            myScroll = new IScroll('#scroll-left', { mouseWheel: false, deceleration: 0.001, click: Utils.iScrollClick() })
        })
    }
}
</script>

<style lang="less" scoped>
@import "../../assets/css/common.less";
.page-content{
    position: relative;
    background-color: #fff;
}

.selectcity-top{
    padding-left: 0.16rem;
    height: .88rem;
    line-height: 0.88rem;
    font-size: 0.28rem;
    color: #c5c7ce;
    background-color: #fff;
    border-bottom: 1px solid #ccc;
    & i{
        margin-right: 0.1rem;
    }
    & span{
        color: #3d4550;
    }
}
</style>



